import axios from "axios";
export const authenticateUser = (p_user, p_password) => {
  let userData = {};
  userData.username = p_user;
  userData.password = p_password;

  return new Promise((resolve, reject) => {
    // let base_url = BASE_URL;
    debugger;
    // let full_url = base_url + "users/authenticate";
    axios
      .post("http://localhost:8002/api/userMenu/getUserMenu", userData)
      .then(response => {
        debugger;
        resolve({ users: response.data });
      });
    //   .catch(response => {
    //     reject("Invalid UserName or Password");
    //   });
  });
};
