import React, { useState } from "react";
import { Grid, Button, TextField } from "@material-ui/core";
import { withRouter } from "react-router-dom";
import AccountCircle from "@material-ui/icons/AccountCircle";
import InputAdornment from "@material-ui/core/InputAdornment";
import LockIcon from "@material-ui/icons/Lock";
// styles
import useStyles from "./styles";
//import axios from "axios";
// context
//import { useUserDispatch, loginUser } from "../../context/UserContext";
import { login, setuserdetails } from "../../context/UserAuth";
import { authenticateUser } from "../../services/profile/loginservice";
function Login(props) {
  var classes = useStyles();

  // global
  //var userDispatch = useUserDispatch();

  // local
  // var [isLoading, setIsLoading] = useState(false);
  // var [error, setError] = useState(null);
  // var [activeTabId, setActiveTabId] = useState(0);
  // var [nameValue, setNameValue] = useState("");
  var [loginValue, setLoginValue] = useState("");
  var [passwordValue, setPasswordValue] = useState("");
  // var user = {
  //   username: loginValue,
  //   password: passwordValue
  // };
  const fnloginUser = async () => {
    let authresponse;
    authresponse = await authenticateUser(loginValue, passwordValue);
    if (authresponse !== undefined) {
      login(authresponse.users.data);
      props.history.push("/app/orders");
      // setuserdetails(authresponse);
    }
  };
  // let fnloginUser = e => {
  //   let status = true;
  //   // axios
  //   //   .post(
  //   //     `https://lingering-forest-5134.getsandbox.com:443/order/authentication`,
  //   //     user
  //   //   )
  //   //   .then(res => {
  //   //     debugger;
  //   //     console.log(res);
  //   //     console.log(res.data);
  //   //     const { history } = this.props;
  //   //     if (res.status) {
  //   //       history.push("/navbar");
  //   //     }
  //   //   });
  //   let authresponse;
  //   authresponse = authenticateUser(loginValue, passwordValue);
  //   login(authresponse);
  //   props.history.push("/app/orders");
  // };

  return (
    <Grid className={classes.container} style={{ background: "white" }}>
      <div className={classes.formContainer}>
        <div className={classes.form}>
          <div
            classNam="logo-img"
            style={{ width: "200px", padding: "10px", margin: "0 auto" }}
          >
            <img src="https://www.shopmyexchange.com/ux-responsive/images/exchange_logo.svg" />
          </div>
          <TextField
            // onChange={this.handleUserNameInput}
            // value={this.state.userName}
            variant="outlined"
            margin="normal"
            required
            id="email"
            placeholder="Username"
            name="email"
            autoComplete="email"
            autoFocus
            value={loginValue}
            onChange={e => setLoginValue(e.target.value)}
            type="email"
            fullWidth
            // style={{ background: "#09396d" }}
            InputProps={{
              startAdornment: (
                <InputAdornment
                  position="start"
                  style={{ background: "#09396d" }}
                >
                  <AccountCircle style={{ color: "#09396d" }} />
                </InputAdornment>
              )
            }}
          />
          <TextField
            // onChange={this.handleUserNameInput}
            // value={this.state.userName}
            variant="outlined"
            margin="normal"
            required
            id="password"
            value={passwordValue}
            onChange={e => setPasswordValue(e.target.value)}
            name="password"
            autoComplete="password"
            autoFocus
            type="password"
            fullWidth
            // style={{ background: "#09396d" }}
            InputProps={{
              startAdornment: (
                <InputAdornment
                  position="start"
                  style={{ background: "#09396d" }}
                >
                  <LockIcon />
                </InputAdornment>
              )
            }}
          />

          <Button
            disabled={loginValue.length === 0 || passwordValue.length === 0}
            onClick={() => fnloginUser()}
            variant="contained"
            color="primary"
            size="large"
            style={{ background: "#11375e", color: "white", width: "100%" }}
          >
            Log In
          </Button>
        </div>
      </div>
    </Grid>
  );
}

export default withRouter(Login);
