import React from "react";
const TOKEN_KEY = "jwt-erpa";
const USER_KEY = "userObj";

// var UserStateContext = React.createContext();
// var UserDispatchContext = React.createContext();

export const login = authresponse => {
  localStorage.setItem(TOKEN_KEY, authresponse.uuid);
  localStorage.setItem(USER_KEY, JSON.stringify(authresponse));
  localStorage.setItem("isAuthenticated", true);
  //localStorage.setItem('token', authresponse.users.token);
  //localStorage.setItem('user', authresponse.users);
};

export const logout = () => {
  //debugger
  localStorage.removeItem(TOKEN_KEY);
  localStorage.removeItem("isAuthenticated");
  //localStorage.removeItem("token");
  //localStorage.removeItem("user");
  return;
};

export const isLogin = () => {
  if (localStorage.getItem(TOKEN_KEY)) {
    return true;
  }

  return false;
};

export const getLoginToken = () => {
  if (localStorage.getItem(TOKEN_KEY)) {
    return localStorage.getItem(TOKEN_KEY);
  }

  return "";
};

