import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import TextFieldInput from "../../components/common/textfield";
//import Paper from "@material-ui/core/Paper";
const useStyles = makeStyles(theme => ({
  root: {},
  paper: {
    padding: theme.spacing(4),
    textAlign: "center",
    color: theme.palette.text.secondary
  }
}));

export default function FormPropsTextFields() {
  const classes = useStyles();

  return (
    <form className={classes.root} noValidate autoComplete="off">
      <Grid container spacing={4}>
        <Grid item xs={6} sm={4}>
          <TextFieldInput label="First Name" />
        </Grid>
        <Grid item xs={6} sm={4}>
          <TextFieldInput label="Last Name" />
        </Grid>
        <Grid item xs={6} sm={4}>
          <TextFieldInput label="Phone No." />
        </Grid>
      </Grid>
      <Grid container spacing={4}>
        <Grid item xs={6} sm={4}>
          <TextFieldInput label="Email" />
        </Grid>
        <Grid item xs={6} sm={4}>
          <TextFieldInput label="State" />
        </Grid>
        <Grid item xs={6} sm={4}>
          <TextFieldInput label="Zip Code" />
        </Grid>
      </Grid>
    </form>
  );
}
